html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
button {
  padding: 0;
  border: none;
  background: none;
  outline: none;
}
* {
  box-sizing: border-box;
}
/* Animations */
@keyframes fa-spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(359deg);
    transform: rotate(359deg);
  }
}
@keyframes shake {
  10%,
  90% {
    transform: translate3d(-1px, 0, 0);
  }
  20%,
  80% {
    transform: translate3d(2px, 0, 0);
  }
  30%,
  50%,
  70% {
    transform: translate3d(-4px, 0, 0);
  }
  40%,
  60% {
    transform: translate3d(4px, 0, 0);
  }
}
@keyframes zoom-in {
  0%,
  100% {
    transform: translateZ(0);
  }
  30%,
  70% {
    transform: translateZ(100px);
  }
}
@keyframes zoom-out {
  0%,
  100% {
    transform: translateZ(0);
  }
  30%,
  70% {
    transform: translateZ(-100px);
  }
}
.pulse {
  -webkit-animation: fa-spin 1s infinite steps(8);
  animation: fa-spin 1s infinite steps(8);
}
/* Typography */
body {
  font-family: Roboto, sans-serif;
  font-weight: 300;
}
h2 {
  margin: 100px 0;
  font-family: Roboto, sans-serif;
  font-size: 60px;
  font-weight: 700;
  text-align: center;
}
h3 {
  margin: 25px 0 20px;
  font-family: Roboto, sans-serif;
  font-size: 24px;
  font-weight: 600;
}
h4 {
  font-weight: 600;
}
code {
  font-family: 'Consolas';
  font-size: 14px;
}
.material-icons {
  font-size: inherit;
}
/* Mixins */
.clear:before,
.clear:after {
  content: "";
  display: table;
  border-collapse: collapse;
}
.clear:after {
  clear: both;
}
.mat-elevation-2dp {
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
}
.mat-elevation-3dp {
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.14), 0 1px 8px 0 rgba(0, 0, 0, 0.12), 0 3px 3px -2px rgba(0, 0, 0, 0.4);
}
.mat-elevation-4dp {
  box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12), 0 2px 4px -1px rgba(0, 0, 0, 0.4);
}
.mat-elevation-6dp {
  box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.14), 0 1px 18px 0 rgba(0, 0, 0, 0.12), 0 3px 5px -1px rgba(0, 0, 0, 0.4);
}
.mat-elevation-8dp {
  box-shadow: 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12), 0 5px 5px -3px rgba(0, 0, 0, 0.4);
}
.mat-elevation-8dp.light {
  box-shadow: 0 0 0 0 transparent, 0 0 0 0 transparent, 0 2px 15px rgba(0, 0, 0, 0.3);
}
.mat-elevation-12dp {
  box-shadow: 0 12px 16px 1px rgba(0, 0, 0, 0.14), 0 4px 22px 3px rgba(0, 0, 0, 0.12), 0 6px 7px -4px rgba(0, 0, 0, 0.4);
}
.mat-elevation-16dp {
  box-shadow: 0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.4);
}
.mat-elevation-24dp {
  box-shadow: 0 24px 38px 3px rgba(0, 0, 0, 0.14), 0 9px 46px 8px rgba(0, 0, 0, 0.12), 0 11px 15px -7px rgba(0, 0, 0, 0.4);
}
/* Main */
html {
  width: 100%;
  height: 100%;
}
body {
  position: relative;
  width: 100%;
  min-height: 100%;
  background: #eaeaea;
}
.primary-toolbar {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 999;
  height: 64px;
  margin: 0 -20px;
  padding: 0 36px;
  background: #2196f3;
  box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12), 0 2px 4px -1px rgba(0, 0, 0, 0.4);
}
.primary-toolbar .title {
  line-height: 64px;
  font-size: 18px;
  font-weight: 400;
  color: #fff;
  white-space: nowrap;
}
.content {
  padding: 64px 0 24px;
}
.card {
  display: inline-block;
  width: 100%;
  margin-bottom: 20px;
  background: #fff;
  border-radius: 2px;
  transition: box-shadow 0.24s ease-in-out;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
}
.card:hover {
  box-shadow: 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12), 0 5px 5px -3px rgba(0, 0, 0, 0.4);
}
.card:hover.light {
  box-shadow: 0 0 0 0 transparent, 0 0 0 0 transparent, 0 2px 15px rgba(0, 0, 0, 0.3);
}
.btn {
  position: relative;
  display: inline-block;
  min-width: 64px;
  height: 36px;
  line-height: 36px;
  margin: 6px;
  padding: 0 16px;
  vertical-align: middle;
  font-size: 14px;
  font-weight: 500;
  text-transform: uppercase;
  text-align: center;
  color: #212121;
  background: #fff;
  border-radius: 2px;
  cursor: pointer;
  border: none;
  outline: none;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
  -webkit-transition: 0.135s all ease;
  transition: 0.135s all ease;
  -webkit-transition-delay: 0s;
  transition-delay: 0s;
}
.btn:active {
  box-shadow: 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12), 0 5px 5px -3px rgba(0, 0, 0, 0.4);
}
.btn:active.light {
  box-shadow: 0 0 0 0 transparent, 0 0 0 0 transparent, 0 2px 15px rgba(0, 0, 0, 0.3);
}
.btn-flat {
  box-shadow: none;
}
.btn-flat:hover {
  background: #eee;
}
.btn-fab {
  min-width: 56px;
  width: 56px;
  height: 56px;
  padding: 0;
  border-radius: 50%;
  box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.14), 0 1px 18px 0 rgba(0, 0, 0, 0.12), 0 3px 5px -1px rgba(0, 0, 0, 0.4);
}
.btn-fab i {
  font-size: 24px;
  line-height: 56px;
}
.btn-fab:active {
  box-shadow: 0 12px 16px 1px rgba(0, 0, 0, 0.14), 0 4px 22px 3px rgba(0, 0, 0, 0.12), 0 6px 7px -4px rgba(0, 0, 0, 0.4);
}
.btn + .btn {
  margin-left: 0;
}
.btn.btn-icon {
  position: relative;
}
.btn.btn-icon .text,
.btn.btn-icon .icon {
  display: inline-block;
  height: 100%;
}
.btn.btn-icon .text {
  position: relative;
  padding-right: 37px;
  line-height: inherit;
  left: 0;
  -webkit-transition: all 0.25s cubic-bezier(0.31, -0.105, 0.43, 1.4);
  transition: all 0.25s cubic-bezier(0.31, -0.105, 0.43, 1.4);
  -webkit-transition-delay: 0s;
  transition-delay: 0s;
}
.btn.btn-icon .text:after {
  content: '';
  background-color: rgba(0, 0, 0, 0.4);
  width: 2px;
  height: 60%;
  position: absolute;
  top: 20%;
  right: 25px;
  box-shadow: 0px 1px 0 rgba(255, 255, 255, 0.25), 0px -1px 0 rgba(0, 0, 0, 0.35);
  background-color: #cccccc;
}
.btn.btn-icon .icon {
  position: absolute;
  top: 0;
  right: 0;
  padding: 0 25px;
  text-align: center;
  -webkit-transition: all 0.25s cubic-bezier(0.31, -0.105, 0.43, 1.4);
  transition: all 0.25s cubic-bezier(0.31, -0.105, 0.43, 1.4);
  -webkit-transition-delay: 0s;
  transition-delay: 0s;
}
.btn.btn-icon .icon i {
  height: 20px;
  vertical-align: middle;
  transition: height 0.1s ease, font-size 0.1s ease;
}
.btn.btn-icon .icon i.success,
.btn.btn-icon .icon i.error {
  display: none;
}
.btn.btn-icon.success .text,
.btn.btn-icon.error .text,
.btn.btn-icon:hover .text {
  transform: translateX(-100%);
  opacity: 0;
}
.btn.btn-icon.success .icon,
.btn.btn-icon.error .icon,
.btn.btn-icon:hover .icon {
  right: 50%;
  transform: translateX(50%);
}
.btn.btn-icon.success .icon i,
.btn.btn-icon.error .icon i,
.btn.btn-icon:hover .icon i {
  font-size: 24px;
  height: 26px;
}
.btn.red {
  background: #f44336;
  text-shadow: 0px 1px 0 rgba(255, 255, 255, 0.25), 0px -1px 0 rgba(0, 0, 0, 0.35);
  color: #eeeeee;
}
.btn.red.btn-icon .text:after {
  box-shadow: 0px 1px 0 rgba(255, 255, 255, 0.25), 0px -1px 0 rgba(0, 0, 0, 0.35);
