}
.todo-builder {
  position: relative;
}
.todo-builder .todo-builder--bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 150px;
  background: #2196f3;
}
.todo-builder .content-wrapper {
  position: relative;
  width: 1160px;
  margin: 0 auto;
  padding: 105px 0;
}
.todo-builder .content-wrapper .btn.build {
  position: absolute;
  top: 150px;
  left: -86px;
  margin: 0;
  transform: translateY(-50%);
}
.todo-builder .todo-board {
  position: relative;
}
.todobox {
  position: relative;
  display: inline-block;
  width: 100%;
  margin-bottom: 20px;
  background: #fff;
  border-radius: 2px;
  transition: box-shadow 0.24s ease-in-out;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
}
.todobox:hover {
  box-shadow: 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12), 0 5px 5px -3px rgba(0, 0, 0, 0.4);
}
.todobox:hover.light {
  box-shadow: 0 0 0 0 transparent, 0 0 0 0 transparent, 0 2px 15px rgba(0, 0, 0, 0.3);
}
.todobox .todo-header {
  position: relative;
  border-bottom: 1px solid #eee;
  z-index: 2;
  margin: 0 -10px;
  padding: 0 10px;
}
.todobox .todo-header .todo-title {
  padding: 10px 15px;
  font-size: 16px;
  line-height: 24px;
  font-weight: 300;
  color: #666;
  outline: none;
  font-weight: 600;
  cursor: default;
}
.todobox .todo-body {
  position: relative;
  overflow: hidden;
}
.todobox .todo-tools {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  align-items: center;
  padding: 8px;
  color: #999;
  opacity: 0;
  -webkit-transition: 0.1s opacity ease;
  transition: 0.1s opacity ease;
  -webkit-transition-delay: 0s;
  transition-delay: 0s;
}
.todobox .todo-tools .tool {
  flex: 0 auto;
  display: block;
  padding: 4px;
  font-size: 18px;
  cursor: pointer;
  color: #999;
  -webkit-transition: all 0.1s ease all ease;
  transition: all 0.1s ease all ease;
  -webkit-transition-delay: 0s;
  transition-delay: 0s;
}
.todobox .todo-tools .tool.clear:hover {
  color: #f44336;
}
.todobox .todo-tools .tool.remove:hover {
  color: #455a64;
}
.todobox .todo-tools .mover {
  padding: 0;
  display: flex;
}
.todobox .todo-tools .mover .tool:hover {
  color: #2196f3;
}
.todobox .todo-footer {
  border-top: 1px solid #eee;
}
.todobox .todo-footer:before,
.todobox .todo-footer:after {
  content: "";
  display: table;
  border-collapse: collapse;
}
.todobox .todo-footer:after {
  clear: both;
}
.todobox .todo-footer .actions {
  float: right;
}
.todobox .todo-footer .actions:before,
.todobox .todo-footer .actions:after {
  content: "";
  display: table;
  border-collapse: collapse;
}
.todobox .todo-footer .actions:after {
  clear: both;
}
.todobox .todo-footer .actions .action {
  float: left;
}
.todobox:hover .todo-tools {
  opacity: 1;
}
.todobox .overflow-wrapper {
  overflow: hidden;
}
.todo-item {
  display: flex;
  align-items: flex-start;
  border-top: 1px solid #eee;
  opacity: 1;
  transition: opacity .250s ease .075s;
}
.todo-item--icon {
  flex: 0 auto;
  display: block;
  width: 36px;
  height: 36px;
  margin: 0;
  text-align: center;
  color: #212121;
  cursor: pointer;
  opacity: .54;
}
.todo-item--icon i {
  line-height: 36px;
  font-size: 20px;
  vertical-align: middle;
}
.todo-item--icon.remove {
  display: none;
  opacity: 0;
}
.todo-item--icon + .todo-item--text {
  padding-left: 0;
}
.todo-item--icon:hover {
  opacity: .87;
}
.todo-item--icon:hover.remove {
  color: #f44336;
}
.todo-item--icon:hover.add {
  color: #2196f3;
}
.todo-item--complete input[type=checkbox] {
  display: none;
}
.todo-item--complete i:before {
  content: 'check_box_outline_blank';
}
.todo-item--complete input[type=checkbox]:checked + i:before {
  content: 'check_box';
}
.todo-item--text,
.todo-item--text .add-box {
  flex: 1 0;
  position: relative;
  z-index: 1;
  width: 100%;
  padding: 10px 0 10px 36px;
  font-family: Roboto, sans-serif;
  font-weight: 300;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.03em;
  outline: none;
  cursor: default;
}
.todo-item .add-box {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding-left: 0;
}
.todo-item .placeholder {
  color: #888;
}
.todo-item:first-child {
  border-top: none;
}
.todo-item.single-line:not(.active) .todo-item--text {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.todo-item.complete:not(.active) .todo-item--text {
  text-decoration: line-through;
}
.todo-item.active .todo-item--icon {
  opacity: .54;
}
.todo-item.more {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
}
.todo-item.hidden {
  opacity: 0;
}
.todo-item:hover .todo-item--icon {
  opacity: .54;
}
.todo-item:hover .todo-item--icon:hover {
  opacity: .87;
}
.clone.animate.over .todo-box {
  -webkit-animation: zoom-in 1s 1;
  animation: zoom-in 1s 1;
}
.clone.animate.under .todo-box {
  -webkit-animation: zoom-out 1s 1;
  animation: zoom-out 1s 1;
}
.clone.over {
  z-index: 3;
}
.clone.over .todo-box {
  box-shadow: 0 0 14px rgba(0, 0, 0, 0.12), 0 4px 10px rgba(0, 0, 0, 0.24);
}
.clone.over .todo-box .todo-tools {
  opacity: 1;
}
.clone.under {
  z-index: 1;
}
.todobox.dialog {
  position: absolute;
  z-index: 100;
  margin: 0;
  visibility: hidden;
  box-shadow: none;
}
.todobox.dialog.animate {
  visibility: visible;
  -webkit-transition: 0.325s all ease-in;
  transition: 0.325s all ease-in;
  -webkit-transition-delay: 0;
  transition-delay: 0;
}
.todobox.dialog.some {
  background: #2196f3;
}
.todobox.dialog.active {
  -webkit-transition: 0.325s all ease-out;
  transition: 0.325s all ease-out;
  -webkit-transition-delay: 0;
  transition-delay: 0;
}
.todobox.dialog:after {
  opacity: 1;
  box-shadow: 0 24px 38px 3px rgba(0, 0, 0, 0.14), 0 9px 46px 8px rgba(0, 0, 0, 0.12), 0 11px 15px -7px rgba(0, 0, 0, 0.4);
}
.dialog {
  position: absolute;
  z-index: 999;
  transform: translateX(-50%) translateY(-50%);
}
.dialog .todobox {
  display: block;
  margin: 0;
  box-shadow: none;
}
.dialog .todobox .todo-item--icon.remove {
  display: block;
}
.dialog .todobox [contenteditable=true] {
  cursor: text;
}
.dialog.animate.show {
  transition: top 0.375s ease, left 0.275s ease, width 0.275s ease, box-shadow 0.375s ease;
  box-shadow: 0 24px 38px 3px rgba(0, 0, 0, 0.14), 0 9px 46px 8px rgba(0, 0, 0, 0.12), 0 11px 15px -7px rgba(0, 0, 0, 0.4);
}
.dialog.animate.show .todo-body {
  overflow-y: auto;
  transition: height .340s ease .035s;
}
.dialog.animate.show .todo-body .todo-item.more {
  opacity: 0;
  visibility: hidden;
  border: none;
  overflow: hidden;
  transition: all .075s ease;
}
.dialog.animate.hide {
  transition: top 0.375s ease, left 0.325s ease 0.05s, width 0.325s ease 0.05s, box-shadow 0.375s ease;
  box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.14), 0 1px 18px 0 rgba(0, 0, 0, 0.12), 0 3px 5px -1px rgba(0, 0, 0, 0.4);
}
.dialog.animate.hide .todo-body {
  transition: height .320s ease;
}
.dialog.animate.hide .todo-body .todo-item .todo-item {
  transition: opacity .150 ease 0;
}
.dialog.animate.hide .todo-body .todo-item .todo-item .more {
  transition: all .150s ease .075s;
}
@media (max-width: 1439px) {
  .content-wrapper {
    width: 900px !important;
  }
}
@media (max-width: 1199px) {
  .content-wrapper {
    width: 760px !important;
  }
}
@media (max-width: 1023px) {
  .content {
    padding-top: 56px !important;
  }
  .primary-toolbar {
